@font-face {
  font-family: icones;
  src: url(./fontes/469IconsFonts-zWy0.ttf);
}

header {
  width: 100%;
  border: 1px solid black;
  background-color: forestgreen;
  color: white;
  padding: 0 10px;
}

section {
  width: 100%;
}

label {
  margin: 5px 0;
  display: flex;
}

input, textarea, select {
  margin: 0 10px;
}

.alingForm {
  flex: 1;
}

.formPrincipal {
  box-sizing: border-box;
  width: 50%;
  border: 1px solid black;
  padding: 10px;
}

.cardSave {
  box-sizing: border-box;
  width: 33%;
  border: 1px solid black;
  padding: 10px;
}

.formFiltro {
  display: flex;
}

.btnDel {
  font-family: 'icones';
  padding: 5px;
}