.card {
  border: 5px solid red;
  border-radius: 5%;
  width: 75%;
  display: flex;
  justify-content:start;
  align-items:center;
  flex-direction: column;
  flex-wrap: wrap;
}

.card h2, .card h4 {
  display: flex;
  justify-content: center;
  margin: 5% 5%;
}

.card h4 {
  border: 1px solid black;
  border-radius: 5%;
  width: 80%;
  height: 100px;
  padding: 2%;
}

.card img {
  width: 90%;
  height: 150px;
}

.atts {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 0 5%;
}

.atts h3 {
  margin: 2% 0;
}

.superTrunfo{
  background-color: yellow;
  height: 40px;
  border-radius: 40%;
  position: absolute;
  display:flex;
  align-items:center;
  margin: 2% 0 0 15%;
}

.superTrunfo p {
  margin: 0;
}